<template>
  <form class="login-form">
    <div class="form-group icon">
      <input name="username" type="text" placeholder="手机号" v-model="uName" v-focus/>
      <img class="form-icon" width="20" src="data:image/png;base64," alt="">
    </div>
    <div class="form-group icon">
      <input name="password" type="password" placeholder="密码" v-model="password" />
      <img class="form-icon" width="20" src="data:image/png;base64," alt="">
    </div>
    <div :class="{'ui-loading-sm': loading}">
      <input type="submit" value="登录" id="loginBtn" @click.prevent="login" />
    </div>
  </form>
</template>

<script>
export default {
  name:'c-login',
  data(){
    return {
      uName:'',
      password: ''
    }
  },
  props:{
    loading: Boolean,
  },
  methods:{
    login(){
      this.$emit('login', {
        uName:this.uName,
        password: this.password
      })
    }
  }
}
</script>



