<template>
  <div class="site-wrap">
    <div class="btn-group" role="group" aria-label="...">
      <button type="button" class="btn btn-default">1</button>
      <button type="button" class="btn btn-default">2</button>

      <div class="btn-group" role="group">
        <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Dropdown
          <span class="caret"></span>
        </button>
        <ul class="dropdown-menu">
          <li><a href="#">Dropdown link</a></li>
          <li><a href="#">Dropdown link</a></li>
        </ul>
      </div>
    </div>

    <div class="login-bg">
        <div class="inner">
          <!-- logo -->
          <div class="site-logo"><img v-bind:src="logo" width="50" class="logo"></div>
          <!-- 头部 -->
          <h1 class="site-header">洽客服务商后台 - <span v-show="currTab == 'LOGIN'">登录</span><span v-show="currTab == 'REGISTER'">注册</span></h1>
          <div class="divider-line"></div>
          <div class="form-box">
            <!-- login & register-->
            <transition :name="slideName">
              <component
              :is="currView"
              :loading="currView=='c-login' ? loginLoading : registerLoading"
              @login="login">
              </component>
            </transition>
          </div>
          <!-- 底部 -->
          <div class="login-footer">
          {{test}}
            <span v-if="currTab == 'LOGIN'" @click="currTab='REGISTER'" class="register-link">我要注册 <img class="icon-arrow-right" width="16" src="data:image/png;base64," alt=""></span>
            <span v-if="currTab == 'REGISTER'" @click="currTab='LOGIN'" class="login-link"><img class="icon-arrow-right" width="16" src="data:image/png;base64," alt=""> 我要登录</span>
          </div>
        </div>
      </div>
      <!-- footer -->
      <footer class="site-footer">蚁国科技 版权所有 © 2016 www.qiakr.com</footer>
  </div>
</template>

<script>
import Vue from 'vue'
import 'element-ui/lib/theme-default/index.css'
import { Message } from 'element-ui'
import Login from './c-login'
import Register from './c-register'
import { apiSession } from '../../api/'
import logo from 'assets/img/logo.png'

Vue.directive('focus', {
  // 当绑定元素插入到 DOM 中。
  inserted: function (el) {
    // 聚焦元素
    el.focus()
  }
})

export default {
  data () {
    return {
      logo: logo,
      loginLoading:false,
      registerLoading: false,
      slideName:'',
      currTab: '',
      currView: '',
      test: 123
    }
  },
  created(){
    // 刷新时保存tab的状态
    let hash = window.location.hash.replace('#','');
    let actionName = ['LOGIN','REGISTER'].includes(hash) ? hash : 'LOGIN';
    this.currTab = actionName
    this.currView = actionName == 'LOGIN' ? 'c-login' : 'c-register'
    this.slideName = actionName == 'LOGIN' ? 'slide-right' : 'slide-left'
  },
  watch:{
    currTab(newV,oldV){
      window.location.hash=newV;
      this.currView = newV == 'LOGIN' ? 'c-login' : 'c-register'
      this.slideName = newV == 'LOGIN' ? 'slide-right' : 'slide-left'
    }
  },
  components:{
    'c-login': Login,
    'c-register': Register
  },
  methods:{
    login({uName, password}){alert(22)
      let _this = this;
      this.loginLoading = true;

      apiSession
        .save({ uName, password, delay:2000})
        .then(res=>{
          if(res.data.status==200){
            Message.success({ message:res.data.message });
            setTimeout(()=>{
              location.href = location.origin
            }, 3000)
          }else{
            Message.error({message:'用户名或密码错误' || res.data.message})
          }
          this.loginLoading = false;
        })
        .catch(error=>{
          this.loginLoading = false;
          console.log(error);
          // Message.error({message:error})
        })
    },
    registor(){}
  }
}
console.log(Vue.test)
</script>

