<template lang="html">
  <form class="register-form">
    <div class="form-group icon">
      <input name="username" type="text" placeholder="请输入手机号码" v-model="phone"/>
      <img class="form-icon" width="20" src="data:image/png;base64," alt="">
    </div>
    <div class="form-group icon">
      <input name="username" type="text" placeholder="请输入您收到的验证码" v-model="code"/>
      <img class="form-icon" width="20" src="data:image/png;base64," alt="">
    </div>
    <div :class="{'ui-loading-sm': loading}">
      <input type="submit" value="注册" id="loginBtn" @click.prevent="register"/>
    </div>
  </form>
</template>

<script>
export default {
  name:'c-register',
  data(){
    return {
      phone: '',
      code: ''
    }
  },
  props:{
    loading: Boolean,
  },
  methods: {
    getCode(){
      this.$emit('getCode', { phone: this.phone })
    },
    register(){
      this.$emit('register', {
        phone: this.phone,
        code: this.code
      })
    }
  },
}
</script>

<style lang="css">
</style>



